import React, { Component } from 'react'
import './List.css'
import Options from './Options'
import './bootstrap.min.css'

export default class List extends Component {
    constructor(props){
        super(props)
        this.state = {
            question:props.item.question,
            correct:props.item.correct,
            answers:props.item.answers,
            id:props.item.questionId
        }
        this.selected = this.selected.bind(this)
    }
    selected(value){
        this.setState({
            answers:[value]
        })
        if(value === this.state.correct){
            this.props.score()
        }
        this.props.count()
    }
    render() {
        return (
            <table className="table table-borderless">
                <tbody>
                <tr>
                    <td style={{"color":"white","fontSize":"18px"}}>
                    {this.props.number+1}) {this.state.question}
                    </td>
                </tr>
                <tr>
                    <Options options={this.state.answers} selected={this.selected}/>
                </tr>
                </tbody>
            </table>
        )
    }
}
