import React, { Component } from 'react'
import './Options.css'
import './bootstrap.min.css'

export default class Options extends Component {
    render() {
        return (
            this.props.options.map((q,index)=>{
            return <td className="option" key={q} onClick={()=>this.props.selected(q)}>({String.fromCharCode(index+65)}
            ) {q}</td>
            })
        )
    }
}
